"use strict";
/* eslint-disable no-unused-vars */

import CustomEvents from 'core/custom_interaction_events';
import Modal from 'core/modal';
import ModalRegistry from 'core/modal_registry';
import {initDb} from "./database";

let registered = false;

let SELECTORS = {
    CANCEL_BUTTON: '[data-action="close"]',
};

/**
 * @constructor
 * @param {object} root The root jQuery element for the modal
 */
function ModalKuet(root) {
    Modal.call(this, root);
}

ModalKuet.TYPE = 'mod_kuet-modal_kuet';
ModalKuet.prototype = Object.create(Modal.prototype);
ModalKuet.prototype.constructor = ModalKuet;

ModalKuet.prototype.registerEventListeners = function() {
    Modal.prototype.registerEventListeners.call(this);
    this.getModal().on(CustomEvents.events.activate, SELECTORS.CANCEL_BUTTON, function() {
        this.destroy();
    }.bind(this));
};

if (!registered) {
    ModalRegistry.register(ModalKuet.TYPE, ModalKuet, 'mod_kuet/modal_kuet');
    registered = true;
}

export const modalKuet = (region) => {
    return new ModalKuet(region);
};

export default {
    modalKuet
};
